import {
  mapMutations,
} from 'vuex';

export default {
  data() {
    return {
      mobile: '',
      password: '',
      logining: false,
      isActive: true,
    };
  },
  onLoad() {

  },
  methods: {
    ...mapMutations(['login']),
    inputChange(e) {
      const { key } = e.currentTarget.dataset;
      this[key] = e.detail.value;
    },
    navBack() {
      uni.navigateBack();
    },
    toRegist() {
      this.$api.msg('去注册');
    },
    async toLogin() {
		uni.showLoading({
			title: '正在登录中'
		});
		this.$request.POST({
			url:this.$api.apiUrl.POST_USER_LOGIN,
			data:{
				username:this.mobile,
				password:this.password
			}
		}).then(res => {
			uni.hideLoading();
			console.log(res.errcode)
			if(res.errcode === 1){
				uni.showToast({
					title: res.errmsg
				});
				uni.navigateTo({
					url: '/pages/user/user'
				});
			}else{
				uni.showToast({
					title:res.errmsg,
					icon:none
				});
			}
		})
    
    //   if (result.status === 1) {
    //     this.login(result.data);
    //     uni.navigateBack();
    //   } else {
    //     this.$api.msg(result.msg);
    //     this.logining = false;
    //   }
    },
    typeSwitch() {
      this.isActive = !this.isActive;
    },
  },

};
